* {
	box-sizing: border-box;
}

body {
	background: #eff3f7;
	margin: 0;
	padding: 0;
}

html, body, #root, .container {
	height: 100%;
	display: flex;
	justify-content: center;
}

.loader {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: 300;
}